.about-me {
  padding-top: 80px;
  padding-bottom: 80px;
  overflow-x: hidden;
}

.about-me-block {
  display: flex;
  justify-content: space-between;
}

.about-me-block .about-me-left {
  width: 34%;
  padding-top: 15px;
}

.about-me-block .about-me-left p {
  font-weight: 600;
  font-size: 36px;
  line-height: 44px;
  color: #0099FF;
}

.about-me-block .about-me-right {
  width: 66%;
}

.about-me-block .about-me-right p {
  font-weight: 600;
  font-size: 44px;
  line-height: 64px;
  color: #1E252B;
  margin-bottom: 48px;
}

.about-me-block .about-me-right p span {
  color: #0099FF;
}

.buy {
  background-image: url("../img/buy-bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
}

.buy-block {
  display: flex;
  justify-content: flex-end;
}

.buy-item {
  width: calc(50% - 30px);
  padding-top: 110px;
  padding-bottom: 105px;
}

.buy-item__text {
  font-size: 44px;
  line-height: 71px;
  font-weight: 600;
  color: #1E252B;
  margin-bottom: 35px;
}

.buy-item__text span {
  color: #0099FF;
}

.cases {
  padding-top: 160px;
  padding-bottom: 80px;
}

.cases h2 {
  margin-bottom: 85px;
}

.cases-grid-item-img {
  position: relative;
  overflow: hidden;
}

.cases-grid-item-img::after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: all 0.3s;
}

.cases-grid-item-img img {
  object-fit: cover;
  object-position: center center;
  width: 100%;
  height: 100%;
  transition: transform 1s ease,opacity 0.5s ease 0.25s;
  transform: scale(1);
}

.cases-grid-item-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 32px;
  text-decoration: none;
}

.cases-grid__title {
  font-size: 36px;
  line-height: 44px;
  font-weight: 600;
  color: #1E252B;
  flex-grow: 1;
  margin-right: 15px;
}

.view-videos {
  padding-top: 80px;
  padding-bottom: 80px;
}

.view-videos h2 {
  margin-bottom: 85px;
}

.view-videos-grid-item-img {
  position: relative;
  margin-bottom: 38px;
}

.view-videos-grid-item-img::after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: all 0.3s;
}

.view-videos-grid-item-img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.view-videos-grid__subtitle {
  font-size: 28px;
  line-height: 38px;
  color: #1E252B;
}

.view-videos .play-video {
  position: absolute;
  content: '';
  right: 12%;
  bottom: 19%;
  transition: all 0.3s;
  z-index: 4;
}

.view-videos .play-video svg {
  display: block;
  width: 68px;
  height: 68px;
  stroke: #ffffff;
  fill: transparent;
  transition: all 0.3s;
}

.read-more {
  padding-top: 80px;
  padding-bottom: 160px;
}

.read-more-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 85px;
}

.read-more-top h2 {
  margin-right: 15px;
  flex-grow: 1;
}

.read-more-list {
  border-top: 1px solid #1E252B;
}

.read-more-list-item + .read-more-list-item {
  border-top: 1px solid #1E252B;
}

.read-more-list-item:last-child {
  border-bottom: 1px solid #1E252B;
}

.read-more-list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 53px;
  padding-bottom: 55px;
}

.read-more-list-item-text {
  flex-grow: 1;
  margin-right: 15px;
}

.read-more-list-item__title {
  display: block;
  width: fit-content;
  font-size: 36px;
  line-height: 44px;
  color: #1E252B;
  font-weight: 600;
  margin-top: 24px;
  text-decoration: none;
}

.connection {
  padding-top: 147px;
  padding-bottom: 160px;
  overflow-x: hidden;
}

.connection h2 {
  width: 100%;
  max-width: 600px;
  margin-bottom: 84px;
}

.connection-block {
  display: flex;
  justify-content: space-between;
}

.connection-text {
  padding-top: 13px;
  width: calc(47% - 20px);
}

.connection-text-link {
  display: flex;
  align-items: center;
}

.connection-text p {
  font-size: 22px;
  line-height: 27px;
  color: #7A7D80;
  margin-right: 36px;
}

.connection-text a {
  text-decoration: none;
  font-size: 28px;
  line-height: 34px;
  color: #09f;
}

.connection-form {
  width: calc(53% - 20px);
}

.connection-form form {
  width: 100%;
  max-width: 725px;
}

.connection-form-check {
  margin-top: 63px;
}

.connection-form button {
  margin-top: 63px;
}

@media (min-width: 1025px) {
  .view-videos-grid-item:hover .view-videos-grid-item-img::after {
    opacity: 1;
  }

  .view-videos-grid-item:hover .play-video {
    right: 50%;
    bottom: 50%;
    transform: translate(50%, 50%);
  }

  .view-videos-grid-item:hover .play-video svg {
    width: 100px;
    height: 100px;
  }

  .read-more-list-item:hover .read-more-list-item-btn {
    animation: btnAction .4s ease-in forwards;
  }
}

@media screen and (max-width: 1640px) {
  .buy-item {
    width: calc(55% - 30px);
  }

  .buy-item__text {
    font-size: 40px;
    line-height: 65px;
  }
}

@media screen and (max-width: 1440px) {
  .about-me {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .about-me-block .about-me-left {
    width: 30%;
  }

  .about-me-block .about-me-left p {
    font-size: 29px;
    line-height: 32px;
  }

  .about-me-block .about-me-right {
    width: 70%;
  }

  .about-me-block .about-me-right p {
    font-size: 36px;
    line-height: 50px;
  }

  .buy-item__text {
    font-size: 37px;
    line-height: 57px;
    margin-bottom: 25px;
  }

  .cases-grid__title {
    font-size: 30px;
    line-height: 36px;
  }

  .read-more {
    padding-top: 40px;
    padding-bottom: 100px;
  }

  .read-more-list-item__title {
    font-size: 32px;
    line-height: 36px;
  }
}

@media screen and (max-width: 1280px) {
  .buy {
    background-position: center right;
  }

  .buy-item {
    padding-top: 95px;
    padding-bottom: 95px;
    width: calc(70% - 30px);
  }

  .cases {
    padding-top: 120px;
    padding-bottom: 60px;
  }

  .cases h2 {
    margin-bottom: 65px;
  }

  .view-videos {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .view-videos h2 {
    margin-bottom: 65px;
  }

  .connection {
    padding-top: 120px;
    padding-bottom: 120px;
  }

  .connection h2 {
    margin-bottom: 64px;
  }

  .connection-form {
    width: calc(55% - 20px);
  }

  .connection-form-check {
    margin-top: 53px;
  }

  .connection-form button {
    margin-top: 53px;
  }

  .connection-text {
    width: calc(45% - 20px);
  }

  .connection-text p {
    font-size: 18px;
    line-height: 22px;
    margin-right: 26px;
  }

  .connection-text a {
    font-size: 18px;
    line-height: 22px;
  }
}

@media screen and (max-width: 1024px) {
  .about-me {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .about-me-block {
    flex-direction: column;
  }

  .about-me-block .about-me-left,
  .about-me-block .about-me-right {
    width: 100%;
  }

  .about-me-block .about-me-left {
    padding-top: 0;
    margin-bottom: 20px;
  }

  .about-me-block .about-me-left p {
    font-size: 26px;
  }

  .about-me-block .about-me-right p {
    font-size: 34px;
    line-height: 48px;
  }

  .buy {
    background-position: 50% center;
  }

  .buy-item {
    width: 55%;
  }

  .buy-item__text span {
    display: block;
  }

  .cases {
    padding-top: 100px;
    padding-bottom: 50px;
  }

  .cases-grid__title {
    font-size: 24px;
    line-height: 28px;
  }

  .view-videos .play-video {
    width: 100%;
    height: 100%;
    right: auto;
    bottom: auto;
    top: 0;
    left: 0;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }

  .view-videos .play-video svg {
    margin-right: 30px;
    margin-bottom: 30px;
  }

  .view-videos-grid__subtitle {
    font-size: 24px;
    line-height: 34px;
  }

  .view-videos-grid-item-img {
    margin-bottom: 25px;
  }

  .read-more-list-item__title {
    font-size: 28px;
  }

  .read-more-top {
    margin-bottom: 65px;
  }

  .connection {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .connection h2 {
    margin-bottom: 44px;
  }

  .connection-block {
    flex-direction: column;
  }

  .connection-text,
  .connection-form {
    width: 100%;
  }

  .connection-text {
    margin-bottom: 40px;
  }

  .connection-form form {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 100%;
  }

  .connection-form form .input-wrap + .input-wrap {
    margin-top: 0;
  }

  .connection-form form .input-wrap {
    width: calc(50% - 20px);
    margin-bottom: 40px;
  }

  .connection-form-check {
    margin-top: 10px;
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .buy {
    background-image: url("../img/buy-bg-mobile.jpg");
    background-position: top left;
  }

  .cases-grid__title {
    font-size: 30px;
    line-height: 36px;
  }

  .view-videos {
    padding-top: 40px;
    padding-bottom: 80px;
  }

  .connection h2 {
    margin-bottom: 35px;
  }

  .connection-text {
    margin-bottom: 25px;
  }

  .connection-form form .input-wrap + .input-wrap {
    margin-top: 15px;
  }

  .connection-form form .input-wrap {
    width: 100%;
    margin-bottom: 0;
  }

  .connection-form-check {
    margin-top: 36px;
  }

  .connection-form button {
    margin-top: 36px;
  }
}

@media screen and (max-width: 575px) {
  .about-me {
    padding-top: 34px;
    padding-bottom: 30px;
  }

  .about-me-block .about-me-left {
    margin-bottom: 28px;
  }

  .about-me-block .about-me-left p {
    font-size: 24px;
    line-height: 29px;
  }

  .about-me-block .about-me-right p {
    font-size: 32px;
    line-height: 44px;
    margin-bottom: 28px;
  }

  .buy-item {
    padding-top: 70px;
    padding-bottom: 70px;
    width: 69%;
  }

  .buy-item__text {
    font-size: 32px;
    line-height: 44px;
  }

  .cases {
    padding-top: 64px;
    padding-bottom: 30px;
  }

  .cases h2 {
    margin-bottom: 40px;
  }

  .cases-grid-item-content {
    padding-top: 30px;
  }

  .cases-grid__title {
    font-size: 24px;
    line-height: 29px;
  }

  .view-videos {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .view-videos h2 {
    margin-bottom: 36px;
  }

  .view-videos-grid__subtitle {
    font-size: 20px;
    line-height: 26px;
  }

  .view-videos .grid.type-1 {
    grid-gap: 37px;
  }

  .view-videos .play-video svg {
    width: 42px;
    height: 42px;
  }

  .read-more {
    padding-top: 30px;
    padding-bottom: 60px;
  }

  .read-more-top {
    margin-bottom: 36px;
  }

  .read-more-list-item {
    padding-top: 32px;
    padding-bottom: 32px;
  }

  .read-more-list-item__title {
    font-size: 24px;
    line-height: 34px;
  }

  .read-more-list-item-btn {
    margin-top: 62px;
  }

  .connection {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  .connection-text p {
    font-size: 16px;
    line-height: 24px;
    margin-right: 24px;
  }

  .connection-text a {
    font-size: 20px;
    line-height: 24px;
  }
}