@import '../variables';

.about-me {
  padding-top: 80px;
  padding-bottom: 80px;
  overflow-x: hidden;
  &-block {
    display: flex;
    justify-content: space-between;
    .about-me-left {
      width: 34%;
      padding-top: 15px;
      p {
        font-weight: 600;
        font-size: 36px;
        line-height: 44px;
        color: $primary;
      }
    }
    .about-me-right {
      width: 66%;
      p {
        font-weight: 600;
        font-size: 44px;
        line-height: 64px;
        color: $secondary;
        margin-bottom: 48px;
        span {
          color: $primary;
        }
      }
    }
  }
}
.buy {
  background-image: url('../img/buy-bg.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
  &-block {
    display: flex;
    justify-content: flex-end;
  }
  &-item {
    width: calc(50% - 30px);
    padding-top: 110px;
    padding-bottom: 105px;
    &__text {
      font-size: 44px;
      line-height: 71px;
      font-weight: 600;
      color: $secondary;
      margin-bottom: 35px;
      span {
        color: $primary;
      }
    }
  }
}
.cases {
  padding-top: 160px;
  padding-bottom: 80px;
  h2 {
    margin-bottom: 85px;
  }
  &-grid {
    &-item {
      &-img {
        position: relative;
        overflow: hidden;
        &::after {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, .6);
          opacity: 0;
          transition: $default;
        }
        img {
          object-fit: cover;
          object-position: center center;
          width: 100%;
          height: 100%;
          transition: transform 1s ease,opacity 0.5s ease 0.25s;
          transform: scale(1);
        }
      }
      &-content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 32px;
        text-decoration: none;
      }
    }
    &__title {
      font-size: 36px;
      line-height: 44px;
      font-weight: 600;
      color: $secondary;
      flex-grow: 1;
      margin-right: 15px;
    }
  }
}
.view-videos {
  padding-top: 80px;
  padding-bottom: 80px;
  h2 {
    margin-bottom: 85px;
  }
  &-grid {
    &-item {
      &-img {
        position: relative;
        margin-bottom: 38px;
        &::after {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, .6);
          opacity: 0;
          transition: $default;
        }
        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }
      }
    }
    &__subtitle {
      font-size: 28px;
      line-height: 38px;
      color: $secondary;
    }
  }
  .play-video {
    position: absolute;
    content: '';
    right: 12%;
    bottom: 19%;
    transition: $default;
    z-index: 4;
    svg {
      display: block;
      width: 68px;
      height: 68px;
      stroke: $white;
      fill: transparent;
      transition: $default;
    }
  }
}
.read-more {
  padding-top: 80px;
  padding-bottom: 160px;
  &-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 85px;
    h2 {
      margin-right: 15px;
      flex-grow: 1;
    }
  }
  &-list {
    border-top: 1px solid $secondary;
    // border-bottom: 1px solid $secondary;
    &-item + &-item {
      border-top: 1px solid $secondary;
    }
    &-item:last-child {
      border-bottom: 1px solid $secondary;
    }
    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-top: 53px;
      padding-bottom: 55px;
      &-text {
        flex-grow: 1;
        margin-right: 15px;
      }
      &__title {
        display: block;
        width: fit-content;
        font-size: 36px;
        line-height: 44px;
        color: $secondary;
        font-weight: 600;
        margin-top: 24px;
        text-decoration: none;
      }
    }
  }
}
.connection {
  padding-top: 147px;
  padding-bottom: 160px;
  overflow-x: hidden;
  h2 {
    width: 100%;
    max-width: 600px;
    margin-bottom: 84px;
  }
  &-block {
    display: flex;
    justify-content: space-between;
  }
  &-text {
    padding-top: 13px;
    width: calc(47% - 20px);
    &-link {
      display: flex;
      align-items: center;
    }
    p {
      font-size: 22px;
      line-height: 27px;
      color: $secondary-light;
      margin-right: 36px;
    }
    a {
      text-decoration: none;
      font-size: 28px;
      line-height: 34px;
      color: #09f;
      // border-bottom: 1px solid #09f;
    }
  }
  &-form {
    width: calc(53% - 20px);
    form {
      width: 100%;
      max-width: 725px;
    }
    &-check {
      margin-top: 63px;
    }
    button {
      margin-top: 63px;
    }
  }
}

@import 'media';
