@media (min-width: 1025px) {
  .view-videos-grid-item:hover {
    .view-videos-grid-item-img::after {
      opacity: 1;
    }
    .play-video {
      right: 50%;
      bottom: 50%;
      transform: translate(50%, 50%);
      svg {
        width: 100px;
        height: 100px;
      }
    }
  }
  .read-more-list-item:hover {
    .read-more-list-item-btn {
      animation: btnAction .4s ease-in forwards;
    }
  }
}

@media screen and (max-width: 1640px) {
  .buy {
    &-item {
      width: calc(55% - 30px);
      &__text {
        font-size: 40px;
        line-height: 65px;
      }
    }
  }
}

@media screen and (max-width: 1440px) {
  .about-me {
    padding-top: 60px;
    padding-bottom: 60px;
    &-block {
      .about-me-left {
        width: 30%;
        p {
          font-size: 29px;
          line-height: 32px;
        }
      }
      .about-me-right {
        width: 70%;
        p {
          font-size: 36px;
          line-height: 50px;
        }
      }
    }
  }
  .buy {
    &-item {
      &__text {
        font-size: 37px;
        line-height: 57px;
        margin-bottom: 25px;
      }
    }
  }
  .cases {
    &-grid {
      &__title {
        font-size: 30px;
        line-height: 36px;
      }
    }
  }
  .read-more {
    padding-top: 40px;
    padding-bottom: 100px;
    &-list {
      &-item {
        &__title {
          font-size: 32px;
          line-height: 36px;
        }
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .buy {
    background-position: center right;
    &-item {
      padding-top: 95px;
      padding-bottom: 95px;
      width: calc(70% - 30px);
    }
  }
  .cases {
    padding-top: 120px;
    padding-bottom: 60px;
    h2 {
      margin-bottom: 65px;
    }
  }
  .view-videos {
    padding-top: 60px;
    padding-bottom: 60px;
    h2 {
      margin-bottom: 65px;
    }
  }
  .connection {
    padding-top: 120px;
    padding-bottom: 120px;
    h2 {
      margin-bottom: 64px;
    }
    &-form {
      width: calc(55% - 20px);
      &-check {
        margin-top: 53px;
      }
      button {
        margin-top: 53px;
      }
    }
    &-text {
      width: calc(45% - 20px);
      p {
        font-size: 18px;
        line-height: 22px;
        margin-right: 26px;
      }
      a {
        font-size: 18px;
        line-height: 22px;     
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .about-me {
    padding-top: 40px;
    padding-bottom: 40px;
    &-block {
      flex-direction: column;
      .about-me-left, .about-me-right {
        width: 100%;
      }
      .about-me-left {
        padding-top: 0;
        margin-bottom: 20px;
        p {
          font-size: 26px;
        }
      }
      .about-me-right {
        p {
          font-size: 34px;
          line-height: 48px;
        }
      }
    }
  }
  .buy {
    background-position: 50% center;
    &-item {
      width: 55%;
      &__text {
        span {
          display: block;
        }
      }
    }
  }
  .cases {
    padding-top: 100px;
    padding-bottom: 50px;
    &-grid {
      &__title {
        font-size: 24px;
        line-height: 28px;
      }
    }
  }
  .view-videos {
    .play-video {
      width: 100%;
      height: 100%;
      right: auto;
      bottom: auto;
      top: 0;
      left: 0;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      svg {
        margin-right: 30px;
        margin-bottom: 30px;
      }
    }
    &-grid {
      &__subtitle {
        font-size: 24px;
        line-height: 34px;
      }
      &-item {
        &-img {
          margin-bottom: 25px;
        }
      }
    }
  }
  .read-more {
    &-list {
      &-item {
        &__title {
          font-size: 28px;
        }
      }
    }
    &-top {
      margin-bottom: 65px;
    }
  }
  .connection {
    padding-top: 80px;
    padding-bottom: 80px;
    h2 {
      margin-bottom: 44px;
    }
    &-block {
      flex-direction: column;
    }
    &-text, &-form {
      width: 100%;
    }
    &-text {
      margin-bottom: 40px;
    }
    &-form {
      form {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        max-width: 100%;
        .input-wrap + .input-wrap {
          margin-top: 0;
        }
        .input-wrap {
          width: calc(50% - 20px);
          margin-bottom: 40px;
        }
      }
      &-check {
        margin-top: 10px;
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .buy {
    background-image: url('../img/buy-bg-mobile.jpg');
    background-position: top left;
  }
  .cases {
    &-grid {
      &__title {
        font-size: 30px;
        line-height: 36px;
      }
    }
  }
  .view-videos {
    padding-top: 40px;
    padding-bottom: 80px;
  }
  .connection {
    h2 {
      margin-bottom: 35px;
    }
    &-text {
      margin-bottom: 25px;
    }
    &-form {
      form {
        .input-wrap + .input-wrap {
          margin-top: 15px;
        }
        .input-wrap {
          width: 100%;
          margin-bottom: 0;
        }
      }
      &-check {
        margin-top: 36px;
      }
      button {
        margin-top: 36px;
      }
    }
  }
}

@media screen and (max-width: 575px) {
  .about-me {
    padding-top: 34px;
    padding-bottom: 30px;
    &-block {
      .about-me-left {
        margin-bottom: 28px;
        p {
          font-size: 24px;
          line-height: 29px;
        }
      }
      .about-me-right {
        p {
          font-size: 32px;
          line-height: 44px;
          margin-bottom: 28px;
        }
      }
    }
  }
  .buy {
    &-item {
      padding-top: 70px;
      padding-bottom: 70px;
      width: 69%;
      &__text {
        font-size: 32px;
        line-height: 44px;
      }
    }
  }
  .cases {
    padding-top: 64px;
    padding-bottom: 30px;
    h2 {
      margin-bottom: 40px;
    }
    &-grid {
      &-item {
        &-content {
          padding-top: 30px;
        }
      }
      &__title {
        font-size: 24px;
        line-height: 29px;
      }
    }
  }
  .view-videos {
    padding-top: 30px;
    padding-bottom: 30px;
    h2 {
      margin-bottom: 36px;
    }
    &-grid {
      &__subtitle {
        font-size: 20px;
        line-height: 26px;
      }
    }
    .grid.type-1 {
      grid-gap: 37px;
    }
    .play-video {
      svg {
        width: 42px;
        height: 42px;
      }
    }
  }
  .read-more {
    padding-top: 30px;
    padding-bottom: 60px;
    &-top {
      margin-bottom: 36px;
    }
    &-list {
      &-item {
        padding-top: 32px;
        padding-bottom: 32px;
        &__title {
          font-size: 24px;
          line-height: 34px;
        }
        &-btn {
          margin-top: 62px;
        }
      }
    }
  }
  .connection {
    padding-top: 64px;
    padding-bottom: 64px;
    &-text {
      p {
        font-size: 16px;
        line-height: 24px;
        margin-right: 24px;
      }
      a {
        font-size: 20px;
        line-height: 24px;
      }
    }
  }
}